.page {
  font-size: 10px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Базовые стили */
.body {
  // font-family: 'Open Sans', sans-serif;
  font-size: 1.5rem;
  line-height: 2.3rem;
  color: #212121;
  background-color: #BDBDBD;
  display: grid;
  grid-template-rows: min-content auto min-content;
  height: 100vh;
  overflow-x: hidden;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  margin: 0 auto;
  max-width: 1900px;
  padding: 0 15px;
}

.container--inner {
  padding: 0 20px;
}



/* M A I N */
.main {
  .heading {
    margin-bottom: 50px;
    text-align: center;
    font-size: 4.2rem;
    line-height: 1.2;
  }

  @media all and (max-width: 767px) {
    .heading {
      font-size: 2.5rem;
      margin-bottom: 30px;
    }
    .text {
      font-size: 1.2rem;
      line-height: 1.8rem;
    }
  }

  .text__link {
    text-decoration: underline;
  }
  .text__link:hover,
  .text__link:focus,
  .text__link:active {
    text-decoration: none;
  }


  .breadcumbs {
    font-size: 12px;
    display: flex;
    flex-wrap: wrap;
    column-gap: 25px;
    margin-left: 10px;
    margin-bottom: 15px;
  }

  .breadcrumbs-item {
    position: relative;
    text-decoration: underline;
  }

  .breadcrumbs-item:hover {
    text-decoration: none;
  }

  .breadcrumbs-item::before {
    content: "";
    position: absolute;
    width: 6px;
    height: 6px;
    background-color: #212121;
    left: -10px;
    top: 8px;
    transform: rotate(45deg);
  }

  .breadcumbs .breadcrumbs-item:last-of-type {
    opacity: 0.5;
    text-decoration: none;
  }
  
