/* F O O T E R */
.footer {
  font-size: 2rem;
  background-color: #757575;
  margin-top: 50px;

  &__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin: 15px 0;
    column-gap: 15px;
  }

  &__contacts {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;

    a {
      text-decoration: underline;
      line-height: 1.3;
    }

    a:hover,
    a:active,
    a:focus {
      text-decoration: none;
      opacity: 1;
    }
  }

  &__social {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 50px;
    margin-left: 40px;
    // margin: 10px 0;
  }

  &__social-link {
    position: relative;
    cursor: pointer;
    padding: 15px 0;

    svg {
      position: absolute;
      top: 18px;
      left: -30px;
    }

    .svg-git {
      top: 16px;
    }
  }


  &__social-link:hover,
  &__social-link:active,
  &__social-link:focus,
  &__social-link:hover svg,
  &__social-link:active svg,
  &__social-link:focus svg {
    color: #ffffff;
    fill: #ffffff;
  }

  &__copy {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;

  }

  &__copy-link {
    border: 2px solid #212121;
    color: #212121;
    padding: 5px 10px;
    margin-top: 5px;
    opacity: 0.6;
  }
  &__copy-link:hover {
    opacity: 1;
  }
}



@media all and (max-width: 767px) {
  .footer {
    font-size: 1rem;

    &__social {
      gap: 30px;
      margin-left: 0;
      padding-top: 10px;
    }

    &__social-link {
      margin-right: 10px;

      svg {
        width: 30px;
        height: 30px;
        top: 0px;
        left: -10px;
      }

      .svg-git {
        width: 35px;
        height: 35px;
        top: -2px;
      }

      span {
        display: none;
      }
    }


    &__contacts {
      display: flex;
    }
    &__contacts-mail {
      margin-bottom: 5px;
    }
    &__contacts-tel {
      margin-bottom: 5px;
    }

    &__copy-link {
      padding: 0px 5px;
      margin-top: 0;
    }
  }
}