.slider__container {
  overflow: hidden;
  // background-color: darkred;
  margin: 0 auto;
  max-width: 1200px;
  height: auto;
}

.slider__track {
  display: flex;
  background-color: lightyellow;
  transition: all .2s;
  width: auto;
  height: auto;
}

.slider__item {
  background-color: $default-primary-color;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  // max-width: 1200px;
}

