.slider-buttons {
  margin-top: 30px; 
  text-align: center;
  display: flex;
  justify-content: center;
}

.slider__btn {
  display: inline-block;
  color: white;
  background-color: $default-primary-color;
  border: none;
  // padding: 10px 20px;
  cursor: pointer;
  margin: 0;
    padding: 0;
    width: 10%;
    height: 45px;

  &:hover {
  background-color: #B2DFDB;
  color: #212121;
}

 &:nth-child(2) {
  margin-left: 30px;
}
}

 

.slider__item--news {
  color: #ffffff;
  padding: 15px;
  line-height: 150%;
  h2 {
    text-align: center;
    margin-bottom: 20px;
  }

  p {
    margin: 20px 0 20px 0;
  }

  img {
    display: block;
    width: auto;
    height: 10vh;
    margin: 0 auto;
  }

  img:hover,
  img:active,
  img:focus {
    transform: rotate(360deg);
    transition: all 2s;
  }
}

.slider__item--news2 {
  background-color:#180b7d;
  .pulse {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    background-color: transparent;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    position: relative;
    svg {
      width: 60px;
      height: auto;
    }

    svg {
      animation: heart 1.4s linear infinite;
    }

    &::after,
    &::before {
      content: '';
      position: absolute;
      border: 2px solid red;
      left: -15px;
      top: -15px;
      bottom: -15px;
      right: -15px;
      border-radius: 50%;
      opacity: 0;
      animation: pulse 3s linear infinite;
    }
    &::after {
      animation-delay: 1.5s;
    }
    @keyframes pulse {
      0% {
        opacity: 0;
        transform: scale(0.5);
      }
      50% {
        opacity: 1;
      }
      100% {
        opacity: 0;
        transform: scale(1.2);
      }
    }

    @keyframes heart {
      0% {
        
      }
      50% {
        transform: scale(1.2);
      }
      100% {
        transform: scale(1.5);
      }
    }
  }

  p {
    text-align: center;
  }

}

.slider__item-img {
  object-fit: cover;
  width: 100%;
  // max-height: 800px;
  // margin: 10px auto;
