/* H E A D E R */
.header {
  background-color: #00796b;
  color: #ffffff;
  margin-bottom: 40px;
  font-weight: 500;
  font-size: 2rem;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__item--current {
    position: relative;
  }

  &__item--current::after {
    content: '';
    position: absolute;
    height: 2px;
    background-color: #ffffff;
    bottom: 0;
    left: 0;
    right: 0;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    column-gap: 35px;
  }

  &__link {
    display: inline-block;
    color: inherit;
    text-decoration: none;
    padding: 20px 5px;
  }

  &__link.header__link--current {
    display: inline-block;
    color: inherit;
    text-decoration: none;
    // padding: 25px 5px;
    cursor: default;
  }

  &__link:hover,
  &__link:active,
  &__link:focus {
    background-color: #b2dfdb;
    color: #212121;
  }

  &__logo {
    display: inline-block;
    background-color: transparent;
    border-radius: 50%;
    cursor: pointer;
    align-self: flex-start;
    margin: 5px 0;
  }

  &__logo:hover,
  &__logo:active,
  &__logo:focus {
    box-shadow: 0 0 10px 0 #212121,
      0 0 100px 0 #212121 inset;
  }

}

@media all and (max-width: 767px) {
  .header {
    font-size: 1rem;
  &__list {
    column-gap: 15px;
  }
  &__link {
    padding: 10px 5px;
  }
  &__logo-img {
    width: 30px;
    height: 25px;
  }
  }
}












